html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

@media (min-width: 841px) {
  .ma__utility-panel {
    display: flex;
    justify-content: space-between;
    width: 100%; }
    .no-flex .ma__utility-panel {
      display: table; } }

.ma__utility-panel__description {
  font-size: 1.25rem;
  line-height: 1.5;
  margin-bottom: 2em;
  margin-right: 2%; }
  @media (min-width: 841px) {
    .ma__utility-panel__description {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: 250px;
      line-height: 1.3;
      margin-bottom: 0; }
      .no-flex .ma__utility-panel__description {
        display: table-cell;
        vertical-align: middle; } }
  @media (min-width: 1121px) {
    .ma__utility-panel__description {
      flex-basis: 350px; } }
  .ma__utility-panel__description--full {
    flex-basis: 850px; }

.ma__utility-panel__items {
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
  margin-bottom: 1.75em; }
  @media (min-width: 841px) {
    .ma__utility-panel__items {
      display: flex;
      align-items: stretch;
      flex-wrap: nowrap;
      flex-shrink: 2;
      flex-grow: 2;
      margin-bottom: 0;
      flex-basis: calc(100% - 250px); }
      .no-flex .ma__utility-panel__items {
        display: table-cell;
        vertical-align: middle; } }
  @media (min-width: 1121px) {
    .ma__utility-panel__items {
      flex-basis: calc(100% - 350px); } }

.ma__utility-panel__item {
  cursor: pointer;
  font-size: 0;
  margin-bottom: 12px; }
  @media (min-width: 841px) {
    .ma__utility-panel__item {
      display: flex;
      align-items: center;
      flex-basis: 33%;
      flex-grow: 0;
      flex-shrink: 1;
      margin: 0 1%; }
      .no-flex .ma__utility-panel__item {
        display: table-cell; }
      .ma__utility-panel__item:first-child {
        margin-left: 0; }
      .ma__utility-panel__item:last-child {
        margin-right: 0; } }
  .ma__utility-panel__item:hover {
    opacity: .8; }
  @media (min-width: 1121px) {
    .ma__utility-panel__item {
      margin: 0 2%; } }
  .ma__utility-panel__item .ma__decorative-link {
    line-height: 1.125;
    padding: 10px;
    padding-right: 28px; }
  .ma__utility-panel__item a {
    border: none;
    font-size: 1.375rem;
    margin: 0;
    text-align: left; }
    @media (min-width: 841px) and (max-width: 1120px) {
      .ma__utility-panel__item a {
        font-size: 1.25rem; } }
    .ma__utility-panel__item a:hover {
      border: none; }

.ma__utility-panel {
  background-color: #14558f;
  color: white; }
  .ma__utility-panel__description {
    font-weight: 300; }
  .ma__utility-panel__item {
    background-color: rgba(255, 255, 255, 0.2); }
    .ma__utility-panel__item a {
      color: white;
      font-weight: 500; }
    .ma__utility-panel__item svg {
      fill: white; }
