html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.ma__error-msg {
  display: none;
  font-size: 1.125rem;
  line-height: 1.75rem;
  margin-bottom: .5em;
  color: #CD0D0D;
  font-weight: 500; }
  .ma__error-msg--success {
    color: #388557 !important; }
  .ma__error-msg.has-error {
    display: flex !important;
    align-items: center; }
