html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.ma__tab-container {
  display: flex;
  flex-direction: column; }
  .ma__tab-container-head {
    display: inline-flex;
    margin-bottom: 0;
    flex-wrap: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar; }
    .ma__tab-container-head::-webkit-scrollbar {
      display: none; }
  .ma__tab-container-body {
    padding: 1rem;
    border: 1px solid #DCDCDC;
    margin-top: -1px; }
  .ma__tab-container .ma__tab-title {
    cursor: pointer;
    padding: 1.75rem 1.5rem 0.8rem 1.5rem;
    font-size: 1.25rem;
    background-color: unset;
    white-space: nowrap;
    border-left: 1px solid #DCDCDC;
    border-top: 1px solid #DCDCDC;
    border-right: none;
    border-bottom: none;
    margin-bottom: -1px;
    display: block; }
    .ma__tab-container .ma__tab-title:first-child {
      border-left-width: 1px; }
    .ma__tab-container .ma__tab-title:last-child {
      border-right: 1px solid #DCDCDC; }
    .ma__tab-container .ma__tab-title:focus {
      outline: none;
      border-top-color: #14558f !important;
      border-left-color: #DCDCDC !important; }
    .ma__tab-container .ma__tab-title--active {
      border-top: 5px solid #14558f;
      padding-top: 1.5rem;
      font-weight: 700;
      color: #14558f;
      background-color: white;
      z-index: 1; }
  .ma__tab-container .ma__tab-container--nested .ma__tab-container-head {
    border-bottom: 1px solid #DCDCDC; }
  .ma__tab-container .ma__tab-container--nested .ma__tab-container-body {
    border: none; }
  .ma__tab-container .ma__tab-container--nested .ma__tab-title {
    border: none;
    border-bottom: 5px solid;
    border-bottom-color: transparent;
    padding: 1.5rem 1.5rem 0.8rem 1.5rem; }
    .ma__tab-container .ma__tab-container--nested .ma__tab-title--active {
      color: #14558f;
      border-bottom: 5px solid #14558f; }
    .ma__tab-container .ma__tab-container--nested .ma__tab-title:focus {
      outline: none;
      border-bottom-color: #14558f !important; }
