html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.ma__button-search {
  border: 2px solid;
  display: inline-block;
  letter-spacing: 0.1em;
  min-height: 46px;
  padding: 0 14px;
  text-transform: uppercase;
  transition: background-color .4s, color .4s, border .4s, fill .4s;
  white-space: nowrap;
  background-color: #388557;
  border-color: #388557 !important;
  color: white;
  font-size: 14px;
  font-weight: 700; }
  .ma__button-search span,
  .ma__button-search > svg {
    display: inline-block;
    vertical-align: middle; }
  .ma__button-search > svg {
    height: 20px;
    transition: fill .4s;
    width: 20px; }
  .ma__button-search:hover {
    background-color: white;
    border-color: #DCDCDC !important;
    color: #388557; }
    .ma__button-search:hover > svg {
      fill: #388557; }
  .ma__button-search > svg {
    fill: white; }
  .ma__button-search--secondary {
    border: 2px solid;
    display: inline-block;
    letter-spacing: 0.1em;
    min-height: 46px;
    padding: 0 14px;
    text-transform: uppercase;
    transition: background-color .4s, color .4s, border .4s, fill .4s;
    white-space: nowrap;
    background-color: white;
    border-color: #DCDCDC !important;
    color: #388557;
    font-size: 14px;
    font-weight: 700; }
    .ma__button-search--secondary span,
    .ma__button-search--secondary > svg {
      display: inline-block;
      vertical-align: middle; }
    .ma__button-search--secondary > svg {
      height: 20px;
      transition: fill .4s;
      width: 20px; }
    .ma__button-search--secondary:hover {
      background-color: #388557;
      border-color: #388557 !important;
      color: white; }
      .ma__button-search--secondary:hover > svg {
        fill: white; }
    .ma__button-search--secondary > svg {
      fill: #388557; }

.ma__button-icon {
  background-color: white;
  border: 2px solid;
  border-color: #DCDCDC;
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.1em;
  min-height: 46px;
  padding: 0 14px;
  text-transform: uppercase;
  transition: background-color .4s, color .4s, border .4s, fill .4s;
  white-space: nowrap;
  color: #388557; }
  .ma__button-icon.ma__button-capitalized {
    text-transform: capitalize;
    font-size: 16px;
    letter-spacing: 0.02em; }
  .ma__button-icon--expandable > svg {
    width: 25px; }
  .ma__button-icon--expanded > svg {
    transform: rotate(180deg); }
  .ma__button-icon span {
    display: inline-block;
    vertical-align: middle;
    padding-right: 5px; }
  .ma__button-icon > svg {
    display: inline-block;
    vertical-align: middle;
    fill: #535353;
    height: 20px;
    transition: fill .4s; }
  .ma__button-icon.ma__icon-green > svg {
    fill: #388557; }
  .ma__button-icon.ma__icon-small > svg {
    height: 10px; }
  .ma__button-icon--expanded, .ma__button-icon:hover {
    background-color: #388557;
    border-color: #388557;
    color: white; }
    .ma__button-icon--expanded svg, .ma__button-icon:hover svg {
      fill: white; }
